@import 'arui-feather/vars.css';
@import '../vars.css';

/**
 * 1. Avoid the IE 10-11 `min-height` bug.
 * 2. Set `flex-shrink` to `0` to prevent Chrome, Opera, and Safari from
 *    letting these items shrink to smaller than their content's default
 *    minimum size.
 */

.page {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    height: 100vh; /* 1 */
    font-family: var(--font);
    font-weight: var(--font-weight-normal);

    &__header,
    &__content,
    &__footer {
        box-sizing: border-box;
    }

    &__header {
        flex-shrink: 0; /* 2 */
        width: 100%;

        &_theme {
            &_alfa-on-color {
                background-color: var(--corporate-background-color);
            }
        }

        @media (--xlarge) {
            width: calc(1100px + var(--application-menu-sidebar-width));
            margin: 0 auto;
        }
    }

    &__footer {
        flex-shrink: 0; /* 2 */
        width: 100%;

        &_offset {
            @media (--large) {
                padding-left: var(--application-menu-sidebar-width);
            }
        }
    }

    &__content {
        flex: 1 0 auto;  /* 2 */
        width: 100%;
        display: inline-flex;
        flex-direction: column;

        &_offset {
            @media (--large) {
                padding-left: var(--application-menu-sidebar-width);
            }
        }
    }
}
