@import 'arui-feather/vars.css';
@import '../vars-menu.css';
@import '../vars.css';

:root {
    --disabled-link-color-on-white: rgba(11, 31, 53, .4);
    --disabled-link-color-on-color: rgba(255, 255, 255, .4);
}

.application-menu-item {
    @media (--large) {
        font-size: var(--font-menu-item);
        height: var(--application-item-height-large);
        padding: 0 var(--application-menu-padding-large-right) 0 var(--application-menu-padding);
    }

    @media (--small-only), (--medium-only) {
        font-size: var(--font-size-m);
        height: var(--application-item-height-small);
        padding: 0 var(--application-menu-padding-small-right) 0 var(--application-menu-padding-small);
    }

    position: relative;

    display: flex;

    box-sizing: border-box;
    margin-bottom: 1px;
    padding: 0 12px;

    cursor: pointer;
    transition: background-color, color .2s ease-in-out;
    text-decoration: none;

    outline: none;

    -webkit-tap-highlight-color: rgba(11, 31, 53, 0);
    align-items: center;

    &:before {
        @media (--large) {
            display: none;
        }

        content: '';
        position: absolute;
        top: -1px;
        right: 0;
        left: 20px;
        height: 1px;
        background: var(--color-border-separator-alfa-on-color);
    }

    &_selected:before {
        display: none;
    }

    &_selected + &:before {
        display: none;
    }

    &_selected + .application-menu-group > &:first-child:before {
        display: none;
    }

    &__notifications-count {
        background: var(--color-accent);
        color: var(--color-accent-content);
        font-weight: var(--font-weight-bold);
        padding: 1px 7px;
        margin-left: 10px;
        border-radius: 82px;
        font-size: 12px;
    }

    &__notifications-count-dot {
        display: block;
        background: var(--color-accent);
        border-radius: 50%;
        width: 6px;
        height: 6px;
        position: absolute;
        left: 26px;
        top: 50%;
        margin-top: -2px;
    }

    &_theme_alfa-on-color {
        color: var(--color-menu-item-alfa-on-color);

        &.application-menu-item {
            &_hovered {
                color: var(--color-content-accent-alfa-on-color);
            }

            &_selected {
                background-color: rgba(255, 255, 255, .1);
            }

            &_disabled {
                color: var(--disabled-link-color-on-color);
                cursor: default;
            }
        }
    }

    &_theme_alfa-on-white {
        color: var(--color-content-alfa-on-white);

        &.application-menu-item {
            &_hovered {
                color: var(--color-content-accent-alfa-on-white);
            }

            &_selected {
                background-color: rgba(11, 31, 53, .1);
            }

            &_disabled {
                color: var(--disabled-link-color-on-white);
                cursor: default;
            }
        }
    }
}
