@import 'arui-feather/vars.css';

.footer {
    &, *, *:before, *:after {
        box-sizing: border-box;
    }

    &__sub {
        display: flex;
        justify-content: center;
        padding: 20px var(--gap-s);
        text-align: center;
    }
}

.footer_theme_alfa-on-color {
    color: var(--color-content-minor-alfa-on-color);

    .link {
        color: var(--color-content-minor-alfa-on-color);

        &_hovered .link__text {
            border-bottom-color: var(--color-content-minor-alfa-on-color);
        }
    }
}

.footer_theme_alfa-on-white {
    color: var(--color-content-minor-alfa-on-white);

    .link {
        color: var(--color-content-minor-alfa-on-white);

        &_hovered .link__text {
            border-bottom-color: var(--color-content-minor-alfa-on-white);
        }
    }
}
