@import 'arui-feather/vars.css';
@import '../vars.css';

:root {
    --header-popup-gradient:
        color(var(--header-bg-color) a(0)) 0%,
        color(var(--header-bg-color) a(.4)) 40%,
        color(var(--header-bg-color) a(.9)) 75%,
        color(var(--header-bg-color) a(1)) 100%;
}

:root {
    --large-logo-height: 86px;
}

.header {
    position: relative;
    font-family: var(--font);
    font-weight: var(--font-weight-normal);

    &_desktop {
        display: none;
    }

    &_mobile {
        display: block;
    }

    @media (--large) {
        &_desktop {
            display: block;
        }

        /* @TODO не рендерить мобильное меню на десктопе */
        &__modal {
            display: none;
        }

        &_mobile {
            display: none;
        }
    }

    &__sidebar {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        width: var(--application-menu-sidebar-width);
        z-index: 1000;
    }

    &__logo-sidebar {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: var(--large-logo-height);
    }

    &__menu {
        @media (--large) {
            overflow: auto;
            width: 100%;
            height: calc(100% - var(--large-logo-height));
            padding-bottom: 60px;
        }
    }

    &__main {
        background-color: var(--header-bg-color);
        position: relative;
        width: 100%;
        height: 60px;
        transition: background 200ms ease;
        z-index: 999;
        display: flex;

        @media (--large) {
            background-color: transparent;
        }
    }

    &__logo-main {
        display: inline-flex;
        height: 100%;
        align-items: center;
        padding: 0 12px;

        @media (--large) {
            display: none;
        }
    }

    &__right-content {
        display: flex;
        align-items: center;
        height: 100%;
        margin-left: auto;
        padding: 0 var(--gap-s);
        min-width: 0;

        @media (--large) {
            padding: 0 calc(var(--gap-s) * 2);
        }
    }

    &__menu-opener {
        display: flex;
        align-items: center;
        height: 100%;
        font-family: var(--font);
        font-size: var(--font-size-m);
        font-weight: var(--font-weight-normal);
        white-space: nowrap;

        @media (--large) {
            display: none;
        }
    }

    &__mobile-closer {
        position: absolute;
        top: 6px;
        left: 10px;
        cursor: pointer;
    }

    &__menu-modal {
        overflow: auto;
        position: absolute;
        top: 60px;
        left: 0;
        bottom: 0;
        right: 0;
    }

    &__top-content {
        position: relative;
        padding-left: 0;

        @media (--large) {
            padding-left: 240px;
        }
    }

    &__modal.popup_theme_alfa-on-color  .popup__container {
        background-color: var(--header-bg-color);
    }
}

.header_theme_alfa-on-color {
    color: var(--color-content-alfa-on-color);

    .header__main-case,
    .header__sidebar {
        background-color: var(--header-bg-color);
    }
}

.header_theme_alfa-on-white {
    color: var(--color-content-alfa-on-white);

    .header__main-case,
    .header__sidebar {
        background-color: var(--header-bg-color);
    }
}

.popup.header__modal {
    .popup__gradient,
    .popup__gradient {
        &_top {
            background:
                linear-gradient(
                    to top,
                    var(--header-popup-gradient)
                );
        }

        &_bottom {
            background:
                linear-gradient(
                    to bottom,
                    var(--header-popup-gradient)
                );
        }
    }
}

.header__modal .popup__content {
    height: 100%;
}
