@import 'arui-feather/vars.css';

.logo {
    line-height: 0;

    &, *, *:before, *:after {
        box-sizing: border-box;
    }

    &__icon, &__text {
        display: inline-block;
        vertical-align: middle;
        background-repeat: no-repeat;
    }

    &__icon {
        position: relative;
        width: 32px;
        height: 32px;
        background-position: center;

        @media (--large) {
            width: 36px;
            height: 36px;
        }
    }

    &__text {
        width: 112px;
        height: 20px;
        background-position: 6px center;
        background-size: 80% auto;
    }

    &_theme_alfa-on-color &__icon {
        background-image: url(./images/logo__icon_theme_alfa-on-color.svg);
    }

    &_theme_alfa-on-color &__text {
        background-image: url(./images/logo__text_theme_alfa-on-color.svg);
    }

    &_theme_alfa-on-white &__icon {
        background-image: url(./images/logo__icon_theme_alfa-on-white.svg);
    }

    &_theme_alfa-on-white &__text {
        background-image: url(./images/logo__text_theme_alfa-on-white.svg);
    }
}
