@import 'arui-feather/vars.css';
@import '../vars.css';
@import '../vars-menu.css';

.application-menu-group {
    &__icon-btn {
        position: absolute;
        transform: translateY(-50%);
        top: 50%;
        vertical-align: middle;
        text-align: center;
        width: 24px;
        height: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: var(--opacity-minor);

        @media (--large) {
            right: var(--application-menu-padding-large-right);
        }

        @media (--small-only), (--medium-only) {
            right: var(--application-menu-padding-small-right);
        }
    }

    &__inner {
        .application-menu-item {
            @media (--large) {
                padding: 0 var(--application-menu-padding-large-right) 0 calc(var(--application-menu-level-padding) + var(--application-menu-padding));
                min-height: var(--application-item-height-large);
            }

            @media (--small-only), (--medium-only) {
                padding: 0 var(--application-menu-padding-small-right) 0 calc(var(--application-menu-padding-small) + var(--application-menu-level-padding-small));

                /* для 2-ого уровня дизайнеры решили увеличить размер item-a в меню */

                min-height: 52px;
            }
        }

        .application-menu-item:before {
            border-top: none;
        }

        .application-menu-item:last-child {
            margin-bottom: var(--offset-embedded-menu);
        }
    }
}
