@import 'arui-feather/vars.css';
@import '../vars.css';
@import '../vars-menu.css';

.application-menu {
    font-family: var(--font);
    font-weight: var(--font-weight-normal);
    outline: none;

    @media (--small-only), (--medium-only) {
        /* отступ всего компонента от верха меню (от кнопки "Закрыть") */
        margin-top: var(--offset-embedded-menu);
    }
}
